.player {
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.time-control {
    width: 50%;
    display: flex;
    input {
        width: 100%;
        padding: 1rem 0rem;
        color: rgba(0, 0, 0, 0.459);
        font-family:'Courier New', Courier, monospace;
        font-weight: 500;
        font-size: smaller;
    }
    p{
        font-family:'Courier New', Courier, monospace;
        padding: 1rem;
        
    }
}

.play-control{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    width: 30rem;
    svg {
        cursor: pointer;
    }
}